import React from 'react';
import {Link} from 'react-router';
import { connect } from 'react-redux';
import cookie from 'react-cookie';

import { resetToken } from '../actions';

class Header extends React.Component {
	constructor() {
		super();
	}

	renderUser() {
		if (!this.props.account.account) {
			return (
				<li className="dropdown">
					<Link to="#" className="dropdown-toggle" data-toggle="dropdown" role="button"
					>User<b className="caret"></b></Link>
					<ul className="dropdown-menu">
						<li>
							<Link to="/register">ng k</Link>
							<Link to="/login">ng nhp</Link>
						</li>
					</ul>
				</li>
			)
		} else {
			return (
				<li className="dropdown">
					<Link to="#" className="dropdown-toggle" data-toggle="dropdown" role="button"
					>{this.props.account.account.name}<b className="caret"></b></Link>
					<ul className="dropdown-menu">
						<li>
							<Link to="#" onClick={this.props.resetMe.bind(this)}>ng xut</Link>
							<Link to="/posts/new">ng bi mi</Link>
						</li>
					</ul>
				</li>
			)
		}
	}

	render() {
		return (
			<div className="navbar navbar-default">
				<div className="container-fluid">
					<div className="navbar-header">
						<button type="button" className="navbar-toggle" data-toggle="collapse"
								data-target=".navbar-responsive-collapse" >
							<span className="icon-bar"></span>
							<span className="icon-bar"></span>
							<span className="icon-bar"></span>
						</button>
						<Link className="navbar-brand" to="/">Unreal Estate</Link>
					</div>
					<div className="collapse navbar-collapse navbar-responsive-collapse">
						<ul className="nav navbar-nav">
							<li className="dropdown">
								<Link to="#" className="dropdown-toggle" data-toggle="dropdown" role="button"
										>Nh bn<b className="caret"></b></Link>
								<ul className="dropdown-menu">
									<li>
										<Link to="#">Bn bit th</Link>
										<Link to="#">Bn cn h chung c</Link>
										<Link to="#">Bn nh ring</Link>
										<Link to="#">Bn t nn</Link>
										<Link to="#">Bn cc loi bt ng sn khc</Link>
									</li>
								</ul>
							</li>
							<li className="dropdown">
								<Link to="#" className="dropdown-toggle" data-toggle="dropdown" role="button"
										>Nh cho thu<b className="caret"></b></Link>
								<ul className="dropdown-menu">
									<li>
										<Link to="#">Cho thu cn h chung c</Link>
										<Link to="#">Cho thu nh ring</Link>
										<Link to="#">Cho thu nh tr</Link>
										<Link to="#">Cho thu vn phng</Link>
										<Link to="#">Cho thu cc loi bt ng sn khc</Link>
									</li>
								</ul>
							</li>
							<li className="dropdown">
								<Link to="#" className="dropdown-toggle" data-toggle="dropdown" role="button"
										>Tin tc<b className="caret"></b></Link>
								<ul className="dropdown-menu">
									<li>
										<Link to="#">Tin th trng</Link>
										<Link to="#">Tin quy hoch</Link>
										<Link to="#">Chnh sch - Quy nh</Link>
									</li>
								</ul>
							</li>
						</ul>
						<ul className="nav navbar-nav navbar-right">
							{this.renderUser()}
						</ul>
					</div>
				</div>
			</div>
		)
	}
}

const mapStateToProps = (state) => {
	return {
		account: state.account
	}
}

const mapDispatchToProps = (dispatch) => {
	return {
		resetMe: () => {
			cookie.remove('jwtToken');
			dispatch(resetToken());
		}
	}
}
export default connect(mapStateToProps, mapDispatchToProps)(Header);
